.pokemon__card {
  height: 100%;
}

.image__container {
  display: flex;
  justify-content: center;
  align-items: canter;
  height: 100%;
  padding: 20px 0;
}

.card__image {
  transition: all 0.2s ease-in-out;
}

.card__image:hover {
  transform: scale(1.2);
  cursor: pointer;
}

.pokemon__name {
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  padding: 5px;
  margin: 0;
  text-transform: capitalize;
  color: #000;
}
